import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import ProgramSummary from "./ProgramSummary";
import ProgramLoadingPage from "./ProgramLoadingPage";
import styles from "../../Assets/Quick_start_style";
import { Context } from "../../Context";
import SelectInput from "../Fields/SelectInput";
import TextInput from "../Fields/TextInput";
import CheckboxInput from "../Fields/CheckboxInput";
import CreateFieldWrapper from "../Fields/CreateFieldWrapper";
import SearchBar from "../Fields/SearchBar";
import { DateTimePicker } from "../CourseWizard/CreateFields";

class ProgramWizard extends Component {
  constructor(props) {
    super(props);
    let state = {};
    window.props.createProgramSteps.forEach((step, index) => {
      let fields = step.map((fieldName, index) =>
        window.props.fields.find(el => el.name === fieldName)
      );
      state["step" + (index + 1)] = fields;
    });
    this.state = state;
  }

  createFields = (fields = []) => {
    const {
      handleFieldChange,
      handleSelectCourses,
      handleRemoveCourses,
      handleSearchBar,
      newProgram,
      translations,
      courses,
      addProgramDetails
    } = this.context;
    const { classes } = this.props;
    return fields.map((field, key) => {
      switch (field.type) {
        case "text":
          return (
            <CreateFieldWrapper key={key} field={field} >
              <TextInput
                field={field}
                defaultValue={newProgram[field.name] || ""}
                handleFieldChange={handleFieldChange}
                newProgram={newProgram}
              />
            </CreateFieldWrapper>
          );
        case "select":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <SelectInput
                field={field}
                value={newProgram[field.name] || []}
                addCourseDetails={addProgramDetails}
              />
            </CreateFieldWrapper>
          );
        case "checkBox":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <CheckboxInput
                field={field}
                addCourseDetails={addProgramDetails}
                value={newProgram}
              />
            </CreateFieldWrapper>
          );
        case "date":
          return (
            <div
              key={key}
              className={classes.mediumWrapper + " " + classes.fieldWrapper}
            >
              <h3 className={classes.fieldTitle}>
                {translations.stepFields["program_dates_title"]}
              </h3>
              <p>{translations.stepFields["program_dates_title_helper"]}</p>
              <DateTimePicker
                type="start_date"
                addCourseDetails={addProgramDetails}
                translations={translations}
                value={newProgram["start_date"]}
              />
              <DateTimePicker
                type="end_date"
                addCourseDetails={addProgramDetails}
                translations={translations}
                value={newProgram["end_date"]}
              />
            </div>
          );
        case "searchBar":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <SearchBar
                field={field}
                defaultValue={newProgram[`${field.name}_bar`] || ""}
                handleSearchBar={handleSearchBar}
                handleSelectCourses={handleSelectCourses}
                handleRemoveCourses={handleRemoveCourses}
                newProgram={newProgram}
                courses={courses}
              />
            </CreateFieldWrapper>
          );
        default:
          return "";
      }
    });
  };

  // IF ALL FIELDS ARE FILLED, YOU CAN GO TO NEXT PAGE
  validProgramParameters = (fields = []) => {
    let valid = true;
    const { newProgram } = this.context;
    fields.forEach(field => {
      if (field.required && !newProgram[field.name]) {
        valid = false;
      }
    });
    return valid;
  };

  render() {
    const {
      step,
      selectedProgram,
      translations,
      newProgram,
      toggleDialog,
      handleBackClick,
      handleNextClick,
      launchProgramCreation,
      deleteAsset
    } = this.context;
    const { classes } = this.props;
    const fields = this.state["step" + step];
    const disabledNext = !this.validProgramParameters(fields);

    return (
      <>
        <div className={classes.bandeau}>
          {[1, 2, 3].includes(step) ? (
            <Typography>Step {step}/3</Typography>
          ) : (
              <h3 className={classes.bandeauTitle}>
                {translations.stepFields.programSummary}
              </h3>
            )}
          <h3 className={classes.bandeauTitle}>Create Program - Mode</h3>
        </div>
        <div className={classes.createCourseWrapper}>
          {[1, 2, 3, 4].includes(step) &&
            this.createFields(fields, selectedProgram, deleteAsset)}
          {step === 4 && (
            <ProgramSummary
              newProgram={newProgram}
              translations={translations}
              classes={classes}
            />
          )}
          {step === 5 && <ProgramLoadingPage />}
        </div>

        {[1, 2, 3, 4].includes(step) && (
          <div className={classes.createButtonsWrapper}>
            <span>
              {[2, 3, 4].includes(step) && (
                <Button
                  onClick={() => handleBackClick()}
                  className={classes.backButton}
                >
                  {translations["back"]}
                </Button>
              )}

              {[1].includes(step) && (
                // PAGE 1
                <Button
                  disabled={disabledNext}
                  onClick={() => handleNextClick()}
                  className={disabledNext ? classes.disabledBtn : ""}
                  style={{ marginLeft: 10 }}
                >
                  {translations["next"]}
                </Button>
              )}
              {[2, 3].includes(step) && (
                <Button
                  disabled={disabledNext}
                  onClick={() => handleNextClick()}
                  style={{ marginLeft: 10 }}
                >
                  {translations["next"]}
                </Button>
              )}
            </span>
            {step === 4 && (
              <Button
                disabled={disabledNext}
                onClick={e => launchProgramCreation(e)}
              >
                {translations.createProgram || "Create Program"}
              </Button>
            )}
            <Button
              className={classes.cancelButton}
              onClick={() => toggleDialog()}
            >
              {translations["cancel"]}
            </Button>
          </div>
        )}
      </>
    );
  }
}
ProgramWizard.contextType = Context;
export default withStyles(styles)(ProgramWizard);
