import React from "react";
import Moment from "react-moment";
import "moment/locale/fr";
// import { isObject } from 'util';

// GET ALL PROGRAMS PARAMS IN ONE ARRAY
const displayableFields = [].concat.apply(
  [],
  window.props && window.props.createProgramSteps
);
const lmsBase =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? "http://localhost:18010"
    : window.props.lmsBase;

function checkURL(url) {
  return url.match(/\.(jpeg|jpg|gif|png)$/) != null;
}

function ProgramSummary({ newProgram, translations, classes }) {
  console.log(Object.entries(newProgram));
  const getProgramInfoValue = programInfo => {
    let programInfoOutput;
    if (programInfo[1] instanceof Date) {
      programInfoOutput = (
        <Moment
          locale={window.props.currentLanguage === "fr" ? "fr" : "en"}
          format={"LLL"}
        >
          {programInfo[1].toString()}
        </Moment>
      );
    } else if (programInfo[0] === "program_grade") {
      programInfoOutput = newProgram["program_settings"].includes(
        "is_program_graded"
      ) ? (
        <span>{programInfo[1]}%</span>
      ) : (
        <span>{translations.programNotGraded}</span>
      );
    } else if (programInfo[0] === "effort") {
      programInfoOutput = (
        <span>
          {programInfo[1][0]} {translations.hours} {programInfo[1][1]}
          {translations.minutes}
        </span>
      );
    } else if (["program_image", "teacher_image"].includes(programInfo[0])) {
      if (checkURL(lmsBase + programInfo[1])) {
        programInfoOutput = (
          <div>
            <img
              style={{ maxWidth: 150 }}
              src={lmsBase + programInfo[1]}
              alt={programInfo[0]}
            />
          </div>
        );
      } else if (Array.isArray(programInfo[1]) && programInfo[1].length > 0) {
        programInfoOutput = (
          <div>
            <img
              style={{ maxWidth: 150 }}
              src={URL.createObjectURL(programInfo[1][0])}
              alt={programInfo[0]}
            />
          </div>
        );
      } else {
        programInfoOutput = <span>{translations.noImage}</span>;
      }
    } else if (programInfo[0] === "program_downloads") {
      programInfoOutput = (
        <ul>
          {programInfo[1].map((value, key) => (
            <li key={key}>{value.title}</li>
          ))}
        </ul>
      );
    } else {
      if (typeof programInfo[1] === "boolean") {
        programInfoOutput = (
          <span>{translations[programInfo[1].toString()]}</span>
        );
      } else if (programInfo[1] === null) {
        programInfoOutput = (
          <span>{translations.nullValue || "No value provided"}</span>
        );
      } else if (Array.isArray(programInfo[1])) {
        programInfoOutput = programInfo[1].map((option, key) => {
          return (
            <p key={key}>
              {translations["stepFields"][option + "_title"] ||
                option.course_name ||
                option ||
                "Not defined"}
            </p>
          );
        });
      } else {
        programInfoOutput = (
          <span>
            {translations.stepFields[programInfo[1] + "_title"] ||
              programInfo[1]}
          </span>
        );
      }
    }

    if (programInfoOutput) {
      return (
        <div>
          <span className={classes.ProgramSummaryTitle}>
            {translations.stepFields[programInfo[0] + "_title"] ||
              programInfo[0]}
            :
          </span>
          {programInfoOutput}
        </div>
      );
    }
  };

  return (
    <div>
      <h3>
        {translations.verifyProgramInfo ||
          "Please verify program info before launching update"}
      </h3>
      {Object.entries(newProgram)
        .filter(el => displayableFields.includes(el[0]))
        .map((programInfo, key) => (
          <div key={key}>{getProgramInfoValue(programInfo)}</div>
        ))}
    </div>
  );
}

export default ProgramSummary;
