import React from "react";
import "./App.css";
import HomePage from "./Layouts/Home/HomePage";
import CreateConfigurePage from "./Layouts/CourseCreatorEditor/CreateConfigurePage";
import NewProgramPage from "./Layouts/ProgramCreator/NewProgramPage";
import SelectModePage from "./Layouts/Home/SelectModePage";
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core/styles";
import { HashRouter, Route } from "react-router-dom";
import BrowserDetection from "react-browser-detection";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#009FE5"
    },
    secondary: {
      main: "#009FE5"
    }
  },
  overrides: {
    MuiButton: {
      text: {
        fontSize: "0.9rem",
        border: "none",
        borderRadius: 25,
        padding: "8px 25px",
        cursor: "pointer",
        backgroundColor: "#009FE5",
        fontWeight: "bold",
        textTransform: "initial",
        color: "white!important"
      }
    }
  }
});

const StudioApp = () => {
  return (
    <>
      <CssBaseline />
      <ThemeProvider theme={theme}>
        <HashRouter basename="/">
          <Route exact path="/" component={() => <HomePage />} />
          <Route
            path="/create/:referredCourseId?"
            component={props => (
              <CreateConfigurePage {...props} editMode="create" />
            )}
          />
          <Route
            path="/select-mode/"
            component={props => <SelectModePage {...props} />}
          />
          <Route
            path="/configure/:referredCourseId?"
            component={props => (
              <CreateConfigurePage {...props} editMode="configure" />
            )}
          />
          <Route
            path="/create-program"
            component={props => <NewProgramPage {...props} editMode="create" />}
          />
        </HashRouter>
      </ThemeProvider>
    </>
  );
};

const browserHandler = {
  ie: () => (
    <div
      style={{
        height: "100vh",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        fontSize: "1.5rem"
      }}
    >
      Please open this interface on Chrome
    </div>
  ),
  default: browser => <StudioApp />
};

function App() {
  return (
    <div>
      <BrowserDetection>{browserHandler}</BrowserDetection>
    </div>
  );
}

export default App;
