import React, { Component } from "react";
import styles from "../../Assets/Quick_start_style.js";
import { withStyles } from "@material-ui/core/styles";
import { DropzoneArea } from "material-ui-dropzone";
import Button from "@material-ui/core/Button";

const translations = window.props && window.props.translations;

const lmsBase =
  !process.env.NODE_ENV ||
  process.env.NODE_ENV === "development" ||
  window.props.lmsBase === "https://edx.devstack.lms:18000"
    ? "http://localhost:18010"
    : window.props.lmsBase;

function checkURL(url) {
  return url.match(/\.(jpeg|jpg|gif|png)$/) != null;
}

class FileUpload extends Component {
  state = {
    uploadsNumber: 0
  };

  handleFieldChange = file => {
    this.props.addCourseDetails({ name: this.props.field.name, value: file });
  };

  eraseFile = editMode => {
    if (editMode === "configure" && checkURL(lmsBase + this.props.value)) {
      this.props.deleteAsset(this.props.value);
    }
    this.props.addCourseDetails({ name: this.props.field.name, value: [] });
    this.setState(prevState => ({
      uploadsNumber: prevState.uploadsNumber + 1
    }));
  };

  render() {
    const { value, type, editMode } = this.props;
    return (
      <div>
        {editMode === "configure" && checkURL(lmsBase + value) ? (
          <>
            <p style={{ fontStyle: "italic" }}>
              {translations.currentImage || "Current image uploaded"}
            </p>
            <img
              style={{ maxWidth: "300px", paddingTop: 20 }}
              src={lmsBase + value}
              alt="course illustration"
            />
          </>
        ) : (
          <>
            <div className="dropzone">
              <DropzoneArea
                onChange={e => this.handleFieldChange(e)}
                filesLimit={type === "image" ? 1 : 3}
                acceptedFiles={
                  type === "image"
                    ? ["image/*"]
                    : ["image/*", "video/*", "application/*"]
                }
                dropzoneText={translations.dragAndDropInfo}
                showAlerts={false}
                showPreviewsInDropzone={type === "image" ? false : true}
                key={this.state.uploadsNumber}
              />
            </div>
            <div>
              {Array.isArray(value) && value.length > 0 && (
                <>
                  <img
                    style={{ maxWidth: "300px", paddingTop: 20 }}
                    src={URL.createObjectURL(value[0])}
                    alt="course illustration"
                  />
                </>
              )}
            </div>
          </>
        )}
        {((Array.isArray(value) && value.length > 0) ||
          checkURL(lmsBase + value)) && (
          <div style={{ maxWidth: "300px", textAlign: "center" }}>
            <Button onClick={() => this.eraseFile(editMode)}>
              {translations.deleteImage || "Delete Image"}
            </Button>
          </div>
        )}
      </div>
    );
  }
}

export default withStyles(styles)(FileUpload);
