import React from "react";
import Moment from "react-moment";
import "moment/locale/fr";
// import { isObject } from 'util';

const displayableFields = [].concat.apply(
  [],
  window.props && window.props.createSteps
);
const lmsBase =
  !process.env.NODE_ENV || process.env.NODE_ENV === "development"
    ? "http://localhost:18010"
    : window.props.lmsBase;

function checkURL(url) {
  return url.match(/\.(jpeg|jpg|gif|png)$/) != null;
}

function CourseSummary({ newCourse, translations, classes, editMode }) {
  const getCourseInfoValue = courseInfo => {
    let courseInfoOutput;
    if (courseInfo[1] instanceof Date) {
      courseInfoOutput = (
        <Moment
          locale={window.props.currentLanguage === "fr" ? "fr" : "en"}
          format={"LLL"}
        >
          {courseInfo[1].toString()}
        </Moment>
      );
    } else if (courseInfo[0] === "course_grade") {
      courseInfoOutput = newCourse["course_settings"].includes(
        "is_course_graded"
      ) ? (
        <span>{courseInfo[1]}%</span>
      ) : (
        <span>{translations.courseNotGraded}</span>
      );
    } else if (courseInfo[0] === "effort") {
      courseInfoOutput = (
        <span>
          {" "}
          {courseInfo[1][0]} {translations.hours} {courseInfo[1][1]}{" "}
          {translations.minutes}{" "}
        </span>
      );
    } else if (["course_image", "teacher_image"].includes(courseInfo[0])) {
      if (checkURL(lmsBase + courseInfo[1])) {
        courseInfoOutput = (
          <div>
            <img
              style={{ maxWidth: 150 }}
              src={lmsBase + courseInfo[1]}
              alt={courseInfo[0]}
            />
          </div>
        );
      } else if (Array.isArray(courseInfo[1]) && courseInfo[1].length > 0) {
        courseInfoOutput = (
          <div>
            <img
              style={{ maxWidth: 150 }}
              src={URL.createObjectURL(courseInfo[1][0])}
              alt={courseInfo[0]}
            />
          </div>
        );
      } else {
        courseInfoOutput = <span>{translations.noImage}</span>;
      }
    } else if (courseInfo[0] === "course_map") {
      courseInfoOutput = courseInfo[1] ? (
        courseInfo[1].map((section, key) => {
          return (
            <span key={key}>
              <p>{section["title"]}</p>
              {section["subsections"].map((subsection, key) => (
                <p key={key}>{subsection}</p>
              ))}
            </span>
          );
        })
      ) : (
        <span>{translations.no_course_map || "No course map provided"}</span>
      );
    } else if (courseInfo[0] === "course_downloads") {
      courseInfoOutput = (
        <ul>
          {courseInfo[1].map((value, key) => (
            <li key={key}>{value.title}</li>
          ))}
        </ul>
      );
    } else {
      if (typeof courseInfo[1] === "boolean") {
        courseInfoOutput = (
          <span>{translations[courseInfo[1].toString()]}</span>
        );
      } else if (courseInfo[1] === null) {
        courseInfoOutput = (
          <span>{translations.nullValue || "No value provided"}</span>
        );
      } else if (Array.isArray(courseInfo[1])) {
        courseInfoOutput = courseInfo[1].map((option, key) => {
          return (
            <p key={key}>
              {" "}
              {translations["stepFields"][option + "_title"] || option}{" "}
            </p>
          );
        });
      } else {
        courseInfoOutput = (
          <span>
            {translations.stepFields[courseInfo[1] + "_title"] || courseInfo[1]}
          </span>
        );
      }
    }

    if (courseInfoOutput) {
      return (
        <div>
          <span className={classes.courseSummaryTitle}>
            {translations.stepFields[courseInfo[0] + "_title"] || courseInfo[0]}{" "}
            :{" "}
          </span>
          {courseInfoOutput}
        </div>
      );
    }
  };

  return (
    <div>
      {editMode === "create" ? (
        <h3>
          {translations.verifyCourseInfo ||
            "Please verify course info before launching creation"}
        </h3>
      ) : (
        <h3>
          {translations.verifyCourseInfo ||
            "Please verify course info before launching update"}
        </h3>
      )}
      {Object.entries(newCourse)
        .filter(el => displayableFields.includes(el[0]))
        .map((courseInfo, key) => (
          <div key={key}>{getCourseInfoValue(courseInfo)}</div>
        ))}
    </div>
  );
}

export default CourseSummary;
