import React, { Component, Fragment } from "react";
import { withStyles } from "@material-ui/styles";
import { Provider } from "../../Context";
import Header from "../../Components/Header";
import Grid from "@material-ui/core/Grid";
import SideBar from "../../Components/Sidebar/SideBar";
import CourseSearch from "../../Components/CourseSearch";

const styles = theme => ({});

const getInitialSelectedFilters = () => {
  const selectedFilters = {};
  window.props.homeFiltersDetail
    .filter(element => element.checked && element.checked.length > 0)
    .forEach(element => (selectedFilters[element.name] = element.checked));
  return selectedFilters;
};

class HomePage extends Component {
  state = {
    page: 0,
    selectedFilters: getInitialSelectedFilters(),
    filters: window.props.filters,
    translations: window.props.translations,
    colors: window.props.colors
  };

  getContext = () => ({
    ...this.state,
    changePage: this.changePage,
    selectOption: this.selectOption,
    resetFilters: this.resetFilters
  });

  changePage = (event, newValue) => {
    this.setState({ page: newValue });
  };

  resetFilters = () => {
    this.setState({ selectedFilters: [] });
  };

  selectOption = (e, filterName, option) => {
    e.stopPropagation();
    const selectedFilters = this.state.selectedFilters;
    if (selectedFilters && selectedFilters[filterName]) {
      if (selectedFilters[filterName].includes(option)) {
        selectedFilters[filterName] = selectedFilters[filterName].filter(
          filterOption => filterOption !== option
        );
        if (selectedFilters[filterName].length === 0) {
          delete selectedFilters[filterName];
        }
      } else {
        selectedFilters[filterName].push(option);
      }
    } else {
      selectedFilters[filterName] = [option];
    }
    this.setState({
      selectedFilters
    });
  };
  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <Provider value={this.getContext()}>
          <Header />
          <Grid container className={classes.container}>
            {this.state.page !== 2 && <Grid item md={3} xs={12}>
              <SideBar />
            </Grid>}
            <Grid item md={this.state.page === 2 ? 12 : 9} xs={12}>
              <CourseSearch page={this.state.page} />
            </Grid>
          </Grid>
        </Provider>
      </Fragment>
    );
  }
}

export default withStyles(styles)(HomePage);
