import React from "react";
import styles from "../../Assets/Quick_start_style.js";
import { withStyles } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import FormHelperText from "@material-ui/core/FormHelperText";
import MenuItem from "@material-ui/core/MenuItem";
import Grid from "@material-ui/core/Grid";

const translations = window.props && window.props.translations;
const SearchBar = ({
  field,
  defaultValue,
  handleSearchBar,
  handleSelectCourses,
  handleRemoveCourses,
  fixedField,
  newProgram,
  courses
}) => {
  return (
    <Grid container>
      <Grid
        // SEARCH BAR BLOCK
        container
        direction="column"
        item
        md={6}
        xs={12}
      >
        <TextField
          defaultValue={
            translations.stepFields[field.name + "_bar_default_value"]
          }
          margin="normal"
          value={defaultValue}
          InputProps={fixedField ? { readOnly: true } : {}}
          onChange={e => handleSearchBar(`${field.name}_bar`, e.target.value)}
          variant="outlined"
        //   error={wrongId}
        />
        {newProgram.courses_list_bar && courses.length === 0 && (
          <FormHelperText error id="component-error-text">
            {translations.noCourseResult}
          </FormHelperText>
        )}
        {defaultValue &&
          courses.length > 0 &&
          courses.map((course, key) => (
            <MenuItem
              key={key}
              value={course}
              style={{ borderBottom: "1px solid rgba(29,159,229,0.5)" }}
              onClick={() => handleSelectCourses(field.name, course)}
            >
              {course.course_id}
            </MenuItem>
          ))}
      </Grid>
      <Grid
        // COURSE SELECTION BLOCK
        container
        direction="column"
        alignItems="center"
        item
        md={6}
        xs={12}
      >
        {newProgram.courses_list &&
          newProgram.courses_list.map(course => {
            return (
              <Grid
                container
                justify="space-between"
                alignItems="center"
                item
                md={6}
                xs={8}
                key={course.course_id}
              >
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    margin: "5px 0"
                  }}
                >
                  <div style={{ "color": window.props.colors.mainColor, fontWeight: 900 }}>{course.course_name}</div>
                  <div>{course.course_id}</div>
                </div>
                <i
                  onClick={() => handleRemoveCourses(field.name, course)}
                  className="fas fa-times"
                  style={{ color: "red", cursor: "pointer" }}
                ></i>
              </Grid>
            );
          })}
      </Grid>
    </Grid>
  );
};

export default withStyles(styles)(SearchBar);
