import React, { Component } from "react";
import { withStyles } from "@material-ui/styles";
import HeaderNav from "../../Components/HeaderNav";
import { Provider } from "../../Context";
import CourseWizard from "../../Components/CourseWizard/CourseWizard";
import AlertDialog from "../../Components/AlertDialog";
import styles from "../../Assets/Quick_start_style.js";

const lmsBase =
  !process.env.NODE_ENV ||
    process.env.NODE_ENV === "development" ||
    window.props.lmsBase === "https://edx.devstack.lms:18000"
    ? "http://localhost:18010"
    : window.props.lmsBase;

const newCourseBasis = window.props && window.props.courseBasis;
const translations = window.props && window.props.translations;

class CreateConfigurePage extends Component {
  state = {
    translations: window.props.translations,
    courses: window.props.courses || [],
    step: 2,
    selectedCourse: undefined,
    mode: "new-course",
    newCourse: newCourseBasis,
    cancelCreationPopup: false,
    newCourseResponse: undefined,
    blockedFields: []
  };
  componentWillMount() {
    const referredCourseId = this.props.match.params.referredCourseId;
    if (referredCourseId) {
      const selectedCourse = this.state.courses.find(
        course => course.course_id === referredCourseId
      );
      this.getcourseInfo(referredCourseId, selectedCourse);
      this.setState({
        mode: this.props.editMode === "configure" ? "configure" : "re-run",
        step: 1,
        blockedFields:
          this.props.editMode === "configure"
            ? ["org", "course_number", "course_session", "course_pacing"]
            : ["org"]
      });
    }
  }
  setMode = mode => {
    this.setState(prevState => ({
      mode,
      step: prevState.step + 1
    }));
  };

  addCourseDetails = courseDetail => {
    this.setState(prevstate => ({
      newCourse: {
        ...prevstate.newCourse,
        [courseDetail.name]: courseDetail.value
      }
    }));
  };

  handleNextClick = () => {
    this.setState(prevState => ({
      step: prevState.step + 1
    }));
  };

  checkCourseId = async () => {
    let validId = false;
    if (this.props.editMode === "configure") {
      validId = true;
    } else {
      const { newCourse } = this.state;
      const url =
        window.props.api.courseidChecking +
        "course-v1:" +
        newCourse.org.toString() +
        "+" +
        newCourse.course_number.toString() +
        "+" +
        newCourse.course_session.toString();
      const settings = {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        }
      };
      const api_call = await fetch(url, settings);
      const data = await api_call.json();
      validId = data.details === "valid_new_id";
    }
    this.setState(prevState => ({
      newCourse: { ...prevState.newCourse, validCourseId: validId }
    }));
    validId && this.handleNextClick();
  };

  deleteAsset = async asset_url => {
    const url =
      lmsBase + "/assets/" + this.state.newCourse.course_id + asset_url;
    const settings = {
      method: "DELETE",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-CSRFToken": window.props.csrfToken
      }
    };
    const api_call = await fetch(url, settings);
    // const data = await api_call.json();
  };

  getcourseInfo = async (referredCourseId, selectedCourse) => {
    const url =
      !process.env.NODE_ENV || process.env.NODE_ENV === "development"
        ? "http://localhost:18010/tma_apps/quickstart/getCourseInfo/" +
        referredCourseId
        : "/tma_apps/quickstart/getCourseInfo/" + referredCourseId;
    const settings = {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      }
    };
    const api_call = await fetch(url, settings);
    const data = await api_call.json();

    this.setState({
      newCourse: {
        ...selectedCourse,
        course_session: selectedCourse.course_id
          .replace("course-v1:", "")
          .split("+")[2],
        course_number: selectedCourse.course_id
          .replace("course-v1:", "")
          .split("+")[1],
        validCourseId: this.props.editMode === "configure" ? true : false,
        ...data,
        org:
          this.props.editMode === "configure"
            ? data["org"]
            : window.props.zone
              ? window.props.zone
              : data["org"]
      }
    });
  };

  launchCourseCreation = async e => {
    e.preventDefault();
    this.handleNextClick();

    const url = window.props.api.courseCreationUrl;
    console.log(url);
    console.log(this.getFormData());

    const settings = {
      method: "POST",
      body: this.getFormData(),
      contentType: false,
      processData: false
    };


    const api_call = await fetch(url, settings);
    const data = await api_call.json();
    if (data.code !== "404") {
      this.setState({ newCourseResponse: data });
    }
  };

  getCourseEffort = () => {
    return (
      (this.state.newCourse.effort[0] * 3600 || 0) +
      (this.state.newCourse.effort[1] * 60 || 0)
    );
  };

  getFormData = () => {
    let formData = new FormData();
    Object.entries(this.state.newCourse).forEach(courseInfo => {
      if (
        ["course_image", "teacher_image"].includes(courseInfo[0]) &&
        Array.isArray(courseInfo[1])
      ) {
        formData.append(courseInfo[0], this.state.newCourse[courseInfo[0]][0]);
      } else if (courseInfo[0] === "effort") {
        formData.append("effort", this.getCourseEffort());
      } else if (["course_type", "course_settings"].includes(courseInfo[0])) {
        const optionsList = window.props.fields.find(
          elem => elem.name === courseInfo[0]
        ).options;
        optionsList.map(option =>
          formData.append(option, courseInfo[1].includes(option))
        );
      } else if (courseInfo[0] === "course_map") {
        formData.append(
          "course_map",
          JSON.stringify(this.state.newCourse.course_map)
        );
      } else if (courseInfo[0] === "course_downloads") {
        let filesTitles = [];
        this.state.newCourse.course_downloads.forEach((element, key) => {
          formData.append("course_downloads" + key, element.file);
          filesTitles.push(element.title);
        });
        formData.append("download_files_titles", JSON.stringify(filesTitles));
      } else if (courseInfo[0] === "actual_course_downloads") {
        formData.append(
          "actual_course_downloads",
          JSON.stringify(courseInfo[1])
        );
      } else {
        formData.append(courseInfo[0], courseInfo[1]);
      }
    });
    formData.append("editMode", this.props.editMode);
    return formData;
  };

  handleBackClick = () => {
    this.setState(prevState => ({
      step: prevState.step - 1
    }));
  };

  handleFieldChange = (fieldName, fieldValue) => {
    if (["course_session", "course_number"].includes(fieldName)) {
      fieldValue = fieldValue
        .replace(/ /g, "")
        .replace(/[^a-zA-Z0-9\-]/g, "")
        .toLowerCase();
    }
    this.setState(prevState => {
      return {
        newCourse: {
          ...prevState.newCourse,
          [fieldName]: fieldValue,
          validCourseId: true
        }
      };
    });
  };

  toggleDialog = () => {
    this.setState(prevstate => ({
      cancelCreationPopup: !prevstate.cancelCreationPopup
    }));
  };

  eraseNewCourse = () => {
    this.setState(
      {
        newCourse: newCourseBasis,
        step: 1,
        cancelCreationPopup: false,
        selectedCourse: undefined,
        mode: undefined
      },
      (window.location.href = "/")
    );
  };

  getContext = () => {
    return {
      ...this.state,
      handleFieldChange: this.handleFieldChange,
      handleNextClick: this.handleNextClick,
      addCourseDetails: this.addCourseDetails,
      toggleDialog: this.toggleDialog,
      handleBackClick: this.handleBackClick,
      launchCourseCreation: this.launchCourseCreation,
      checkCourseId: this.checkCourseId,
      eraseNewCourse: this.eraseNewCourse,
      editMode: this.props.editMode,
      blockedFields: this.state.blockedFields,
      deleteAsset: this.deleteAsset
    };
  };

  render() {
    const { classes } = this.props;
    // const { step, mode, selectedCourse } = this.state;
    // const courses = this.state.courses.filter(
    //   course =>
    //     course.type !== "vodeclic" && window.props.orgs.includes(course.org)
    // );
    return (
      <>
        <Provider value={this.getContext()}>
          <HeaderNav />
          <div className={classes.createPageWrapper}>
            <CourseWizard />
          </div>
          <AlertDialog
            openDialog={this.state.cancelCreationPopup}
            toggleDialog={this.toggleDialog}
            dialogTitle={translations.cancelCreationTitle}
            dialogContent={translations.cancelCreationContent}
            dialogValid={translations.validCancelCreation}
            dialogCancel={translations.backToCourse}
            dialogAction={this.eraseNewCourse}
          />
        </Provider>
      </>
    );
  }
}

export default withStyles(styles)(CreateConfigurePage);
