import "date-fns";
import React from "react";
import TextField from "@material-ui/core/TextField";
// import Radio from '@material-ui/core/Radio';
// import RadioGroup from '@material-ui/core/RadioGroup';
import FormHelperText from "@material-ui/core/FormHelperText";
// import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from "@material-ui/core/FormControl";

import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker
} from "@material-ui/pickers";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import Slider from "@material-ui/lab/Slider";
import NumberFormat from "react-number-format";
import { withStyles } from "@material-ui/core";
import { DropzoneArea } from "material-ui-dropzone";
import styles from "../../Assets/Quick_start_style.js";

import AddCircleOutline from "@material-ui/icons/AddCircleOutline";
import Clear from "@material-ui/icons/Clear";

import "../../Assets/extraStyle.css";

const FileUpload = withStyles(styles)(
  ({
    field,
    translations,
    classes,
    addCourseDetails,
    value,
    filetype = "image/*",
    limit = 1,
    showPreview = false
  }) => {
    const handleFieldChange = file => {
      addCourseDetails({ name: field.name, value: file });
    };
    return (
      <div className={classes.mediumWrapper + " " + classes.fieldWrapper}>
        {!field.hideTitle && (
          <h3 className={classes.fieldTitle}>
            {translations.stepFields[field.name + "_title"]}
          </h3>
        )}
        <p>{translations.stepFields[field.name + "_helper"]}</p>
        <div>
          <div id="dropzone">
            <DropzoneArea
              onChange={e => handleFieldChange(e)}
              filesLimit={1}
              acceptedFiles={["image/*"]}
              dropzoneText={translations.dragAndDropInfo}
              showAlerts={false}
              showPreviewsInDropzone={false}
              style={{
                fontSize: 14
              }}
            />
          </div>
          <div>
            {value && value[0] && (
              <img
                style={{ maxWidth: "300px", paddingTop: 20 }}
                src={URL.createObjectURL(value[0])}
                alt="course illustration"
              />
            )}
          </div>
        </div>
      </div>
    );
  }
);

const TimeInput = withStyles(styles)(
  ({ field, translations, classes, addCourseDetails, value }) => {
    const handleFieldChange = (name, newValue) => {
      name === "hours"
        ? addCourseDetails({ name: field.name, value: [newValue, value[1]] })
        : addCourseDetails({ name: field.name, value: [value[0], newValue] });
    };
    return (
      <>
        <div className={[classes.fieldWrapper, classes.smallWrapper].join(" ")}>
          {!field.hideTitle && (
            <h3 className={classes.fieldTitle}>
              {translations.stepFields[field.name + "_title"]}
            </h3>
          )}
          <p>{translations.stepFields[field.name + "_helper"]}</p>
          <Grid container>
            <Grid item xs={6}>
              <NumberFormat
                value={value[0]}
                onChange={e => handleFieldChange("hours", e.target.value)}
                className={classes.timeInput}
              />
              <span className={classes.inputLabel}>{translations.hours}</span>
            </Grid>
            <Grid item xs={6}>
              <NumberFormat
                value={value[1]}
                onChange={e => handleFieldChange("minutes", e.target.value)}
                className={classes.timeInput}
              />
              <span className={classes.inputLabel}>{translations.minutes}</span>
            </Grid>
          </Grid>
        </div>
      </>
    );
  }
);

function DateTimePicker({ type, translations, value, addCourseDetails }) {
  function handleDateChange(date) {
    addCourseDetails({ value: date ? date.toISOString() : date, name: type });
  }

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <Grid container justify="space-around">
        <KeyboardDatePicker
          margin="normal"
          id="mui-pickers-date"
          label={
            type === "start_date"
              ? translations["startDate_date"]
              : translations["endDate_date"]
          }
          value={value}
          onChange={handleDateChange}
          KeyboardButtonProps={{
            "aria-label": "change date"
          }}
          cancelLabel={translations.cancel}
          okLabel={translations.confirm}
        />
        <KeyboardTimePicker
          margin="normal"
          id="mui-pickers-time"
          label={
            type === "start_date"
              ? translations["startDate_time"]
              : translations["endDate_time"]
          }
          value={value}
          onChange={handleDateChange}
          KeyboardButtonProps={{
            "aria-label": "change time"
          }}
          cancelLabel={translations.cancel}
          okLabel={translations.confirm}
        />
      </Grid>
    </MuiPickersUtilsProvider>
  );
}

const SelectInput = withStyles(styles)(
  ({
    value = "",
    field,
    translations,
    classes,
    addCourseDetails,
    fixedField
  }) => {
    const handleChange = event => {
      addCourseDetails({ value: event.target.value, name: field.name });
    };
    return (
      <div className={classes.fieldWrapper}>
        <h3 className={classes.fieldTitle}>
          {translations.stepFields[field.name + "_title"]}
        </h3>
        <FormHelperText>
          {translations.stepFields[field.name + "_helper"]}
        </FormHelperText>
        <FormControl variant="outlined" disabled={fixedField}>
          <Select
            value={value}
            onChange={handleChange}
            inputProps={{
              name: field.name
            }}
            className={classes.selectField}
          >
            <MenuItem value="">
              <em></em>
            </MenuItem>
            {field.options.map((option, key) => (
              <MenuItem key={key} value={option}>
                {translations.stepFields[option + "_title"] || option}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>
    );
  }
);

const TextAreaInput = withStyles(styles)(
  ({ value, field, translations, classes, addCourseDetails }) => {
    const handleChange = event => {
      addCourseDetails({ value: event.target.value, name: field.name });
    };
    return (
      <div className={classes.fieldWrapper}>
        <h3 className={classes.fieldTitle}>
          {translations.stepFields[field.name + "_title"]}
        </h3>
        <FormHelperText>
          {translations.stepFields[field.name + "_helper"]}
        </FormHelperText>
        <TextField
          rows="4"
          className={classes.textEditorWrapper}
          multiline
          variant="outlined"
          onChange={handleChange}
          value={value}
        />
      </div>
    );
  }
);

const StyledSlider = withStyles({
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: "#fff",
    marginTop: 10
  },
  track: {
    backgroundColor: window.props && window.props.colors.configureColor,
    height: 24,
    borderRadius: 60
  },
  trackAfter: {
    backgroundColor: window.props && window.props.colors.statisticsColor,
    opacity: 1,
    borderRadius: 60
  },
  container: {
    height: 70
  }
})(Slider);

const GradeInput = withStyles(styles)(
  ({ value, field, translations, classes, addCourseDetails }) => {
    const handleChange = (event, newValue) => {
      addCourseDetails({ value: Math.round(newValue), name: field.name });
    };

    const createGradeBasis = () => {
      let base = [];
      for (let i = 0; i <= 100; i += 10) {
        base.push(<span key={i}>{i}</span>);
      }
      return base;
    };

    return (
      <div className={classes.fieldWrapper}>
        <h3 className={classes.fieldTitle}>
          {translations.stepFields[field.name + "_title"]}
        </h3>
        <FormHelperText>
          {translations.stepFields[field.name + "_helper"]}
        </FormHelperText>
        <p>
          {translations.fail || "Fail Level"} : 0% - {Math.round(value)}%{" "}
        </p>
        <p>
          {translations.pass || "Pass"} : {Math.round(value)}% - 100%{" "}
        </p>
        <StyledSlider
          container={{ height: 60 }}
          value={value}
          onChange={handleChange}
          className={classes.sliderWrapper}
        />
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
        >
          {createGradeBasis()}
        </Grid>
      </div>
    );
  }
);

const JsonEditor = withStyles(styles)(
  ({ classes, value, field, translations, addCourseDetails }) => {
    const handleChange = (
      newValue,
      sectionIndex,
      subsectionIndex = undefined
    ) => {
      let finalValue = [...value];
      if (subsectionIndex !== undefined) {
        finalValue[sectionIndex]["subsections"][subsectionIndex] = newValue;
      } else {
        finalValue[sectionIndex]["title"] = newValue;
      }
      addCourseDetails({ name: field.name, value: finalValue });
    };

    const addSection = () => {
      addCourseDetails({
        name: field.name,
        value: [
          ...value,
          {
            title: translations.new_section || "New Section",
            subsections: [translations.new_subsection || "New Subsection"]
          }
        ]
      });
    };

    const addSubSection = sectionIndex => {
      let modifiedValue = [...value];
      modifiedValue[sectionIndex]["subsections"].push(
        translations.new_subsection || "New subsection"
      );
      addCourseDetails({ name: field.name, value: modifiedValue });
    };

    const deleteElement = (sectionIndex, subsectionIndex = undefined) => {
      let modifiedValue = [...value];
      if (subsectionIndex !== undefined) {
        if (modifiedValue[sectionIndex]["subsections"].length > 1) {
          modifiedValue[sectionIndex]["subsections"] = [
            ...modifiedValue[sectionIndex]["subsections"].splice(
              0,
              subsectionIndex
            ),
            ...modifiedValue[sectionIndex]["subsections"].splice(
              subsectionIndex + 1
            )
          ];
        } else {
          modifiedValue[sectionIndex]["subsections"] = [];
        }
      } else {
        modifiedValue = [
          ...modifiedValue.splice(0, sectionIndex),
          ...modifiedValue.splice(sectionIndex + 1)
        ];
      }
      addCourseDetails({ name: field.name, value: modifiedValue });
    };

    return (
      <div className={classes.fieldWrapper}>
        <h3 className={classes.fieldTitle}>
          {translations.stepFields[field.name + "_title"]}
        </h3>
        <FormHelperText className={classes.helpers}>
          {translations.stepFields[field.name + "_helper"]}
        </FormHelperText>

        <Grid
          container
          direction="column"
          justify="center"
          alignItems="flex-start"
          spacing={2}
        >
          {value.map((mapElement, key1) => {
            return (
              <div key={key1}>
                <Grid item>
                  <TextField
                    value={mapElement["title"]}
                    variant="outlined"
                    onChange={e => handleChange(e.target.value, key1)}
                  />
                  <Clear
                    className={classes.clear}
                    onClick={() => deleteElement(key1)}
                  />
                </Grid>
                {mapElement["subsections"].map((subsection, key2) => (
                  <Grid item key={key2} className={classes.subsections}>
                    <TextField
                      value={subsection}
                      variant="outlined"
                      onChange={e => handleChange(e.target.value, key1, key2)}
                    />
                    <Clear
                      className={classes.clear}
                      onClick={() => deleteElement(key1, key2)}
                    />
                  </Grid>
                ))}
                <span
                  className={classes.sectionsAdd}
                  onClick={e => addSubSection(key1)}
                >
                  <AddCircleOutline />{" "}
                  {translations.addSubsection || "Add Subsection"}
                </span>
              </div>
            );
          })}
          <span className={classes.sectionsAdd} onClick={() => addSection()}>
            <AddCircleOutline /> {translations.addSection || "Add Section"}
          </span>
        </Grid>
      </div>
    );
  }
);

export {
  DateTimePicker,
  SelectInput,
  TextAreaInput,
  GradeInput,
  TimeInput,
  FileUpload,
  JsonEditor
};
