import React from "react";
import styles from "../../Assets/Quick_start_style.js";
import { withStyles } from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";

const translations = window.props && window.props.translations;

const RadioInput = ({
  field,
  classes,
  addCourseDetails,
  value,
  fixedField
}) => {
  const handleChange = name => event => {
    addCourseDetails({ name: name, value: event.target.value });
  };

  return (
    <FormControl component="fieldset">
      <RadioGroup
        aria-label={field.name}
        name={field.name}
        value={value || field.options[0]}
        onChange={handleChange(field.name)}
      >
        {field.options.map((option, key) => (
          <div key={key}>
            <FormControlLabel
              value={option}
              control={<Radio />}
              label={translations.stepFields[option + "_title"] || option}
              disabled={fixedField}
            />
            <FormHelperText>
              {translations.stepFields[option + "_helper"]}
            </FormHelperText>
          </div>
        ))}
      </RadioGroup>
    </FormControl>
  );
};

export default withStyles(styles)(RadioInput);
