import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { withStyles } from "@material-ui/core";
import styles from "../Assets/Quick_start_style.js";
import { Context } from "../Context";

function TabBar({ classes }) {
  return (
    <Context.Consumer>
      {({ changePage, page }) => (
        <AppBar position="static" className={classes.tabWrapper}>
          <Tabs
            value={page}
            onChange={changePage}
            className={classes.tabsWrapper}
          >
            <Tab className={classes.tabTitle} label="Courses" />
            <Tab className={classes.tabTitle} label="Vodeclic" />
            <Tab className={classes.tabTitle} label="Programs" />
          </Tabs>
        </AppBar>
      )}
    </Context.Consumer>
  );
}

export default withStyles(styles)(TabBar);
