import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import { DateTimePicker, GradeInput } from "./CreateFields";
import Button from "@material-ui/core/Button";
import Cartouche from "./Cartouche";
import CourseSummary from "./CourseSummary";
import LoadingPage from "./LoadingPage";
import styles from "../../Assets/Quick_start_style";
import { Context } from "../../Context";

import SelectInput from "../Fields/SelectInput";
import TimeInput from "../Fields/TimeInput";
import JsonEditor from "../Fields/JsonEditor";
import TextInput from "../Fields/TextInput";
import RadioInput from "../Fields/RadioInput";
import CheckboxInput from "../Fields/CheckboxInput";
import TextAreaInput from "../Fields/TextAreaInput";
import UploadInput from "../Fields/UploadInput";
import TextEditor from "../Fields/TextEditorInput";
import MultipleUploadsInput from "../Fields/MultipleUploadsInput";
import CreateFieldWrapper from "../Fields/CreateFieldWrapper";

class CourseWizard extends Component {
  constructor(props) {
    super(props);
    let state = {};
    window.props.createSteps.forEach((step, index) => {
      let fields = step.map((fieldName, index) =>
        window.props.fields.find(el => el.name === fieldName)
      );
      state["step" + (index + 1)] = fields;
    });
    this.state = state;
  }

  createFields = (
    fields = [],
    blockedFields = [],
    editMode,
    deleteAsset
  ) => {
    const {
      handleFieldChange,
      newCourse,
      translations,
      addCourseDetails
    } = this.context;
    const { classes } = this.props;
    return fields.map((field, key) => {
      switch (field.type) {
        case "text":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <TextInput
                field={field}
                defaultValue={newCourse[field.name] || ""}
                handleFieldChange={handleFieldChange}
                fixedField={blockedFields.includes(field.name)}
                newCourse={newCourse}
              />
            </CreateFieldWrapper>
          );
        case "radio":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <RadioInput
                field={field}
                addCourseDetails={addCourseDetails}
                value={newCourse[field.name]}
                fixedField={blockedFields.includes(field.name)}
              />
            </CreateFieldWrapper>
          );
        case "checkBox":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <CheckboxInput
                field={field}
                addCourseDetails={addCourseDetails}
                value={newCourse}
              />
            </CreateFieldWrapper>
          );
        case "imageUpload":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <UploadInput
                field={field}
                addCourseDetails={addCourseDetails}
                value={newCourse[field.name]}
                type="image"
                editMode={editMode}
                deleteAsset={deleteAsset}
              />
            </CreateFieldWrapper>
          );
        case "multipleUploads":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <MultipleUploadsInput
                field={field}
                value={newCourse[field.name] || []}
                addCourseDetails={addCourseDetails}
                editMode={editMode}
                actualDownloads={newCourse["actual_" + field.name] || []}
                deleteAsset={deleteAsset}
              />
            </CreateFieldWrapper>
          );
        case "date":
          return (
            <div
              key={key}
              className={classes.mediumWrapper + " " + classes.fieldWrapper}
            >
              <h3 className={classes.fieldTitle}>
                {translations.stepFields["course_dates_title"]}
              </h3>
              <p>{translations.stepFields["course_dates_title_helper"]}</p>
              <DateTimePicker
                type="start_date"
                addCourseDetails={addCourseDetails}
                translations={translations}
                value={newCourse["start_date"]}
              />
              <DateTimePicker
                type="end_date"
                addCourseDetails={addCourseDetails}
                translations={translations}
                value={newCourse["end_date"]}
              />
            </div>
          );
        case "select":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <SelectInput
                field={field}
                value={newCourse[field.name] || []}
                addCourseDetails={addCourseDetails}
                fixedField={blockedFields.includes(field.name)}
              />
            </CreateFieldWrapper>
          );
        case "textField":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <TextAreaInput
                field={field}
                addCourseDetails={addCourseDetails}
                value={newCourse[field.name]}
              />
            </CreateFieldWrapper>
          );
        case "grade":
          return (
            <GradeInput
              key={key}
              field={field}
              translations={translations}
              addCourseDetails={addCourseDetails}
              value={newCourse[field.name]}
            />
          );
        case "time":
          return (
            <TimeInput
              key={key}
              field={field}
              translations={translations}
              addCourseDetails={addCourseDetails}
              value={newCourse[field.name] || []}
            />
          );
        case "jsonEditor":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <JsonEditor
                field={field}
                value={newCourse[field.name]}
                addCourseDetails={addCourseDetails}
              />
            </CreateFieldWrapper>
          );
        case "textEditor":
          return (
            <CreateFieldWrapper key={key} field={field}>
              <TextEditor
                field={field}
                value={newCourse[field.name]}
                addCourseDetails={addCourseDetails}
              />
            </CreateFieldWrapper>
          );
        default:
          return "";
      }
    });
  };

  validCourseParameters = (fields = []) => {
    let valid = true;
    const { newCourse } = this.context;
    fields.forEach(field => {
      if (field.required && !newCourse[field.name]) {
        valid = false;
      }
    });
    return valid;
  };

  render() {
    const {
      mode,
      step,
      selectedCourse,
      translations,
      handleNextClick,
      newCourse,
      toggleDialog,
      handleBackClick,
      launchCourseCreation,
      checkCourseId,
      editMode,
      blockedFields,
      deleteAsset
    } = this.context;
    const { classes } = this.props;
    const fields = this.state["step" + step];
    const disabledNext = !this.validCourseParameters(fields);
    return (
      <>
        <div className={classes.bandeau}>
          {[1, 2, 3, 4].includes(step) ? (
            <Typography>Step {step}/4</Typography>
          ) : (
              <h3 className={classes.bandeauTitle}>
                {translations.stepFields.courseSummary}
              </h3>
            )}
          <h3 className={classes.bandeauTitle}>
            {editMode === "create"
              ? "Create Course - Mode " + mode
              : "Configure Course"}
          </h3>
        </div>
        <div className={classes.createCourseWrapper}>
          {[2, 3, 4].includes(step) && (
            <Cartouche translations={translations} newCourse={newCourse} />
          )}
          {[1, 2, 3, 4, 5].includes(step) &&
            this.createFields(
              fields,
              selectedCourse,
              blockedFields,
              editMode,
              deleteAsset
            )}
          {step === 5 && (
            <CourseSummary
              newCourse={newCourse}
              translations={translations}
              classes={classes}
              editMode={editMode}
            />
          )}
          {step === 6 && <LoadingPage />}
        </div>

        {[1, 2, 3, 4, 5].includes(step) && (
          <div className={classes.createButtonsWrapper}>
            <span>
              {[2, 3, 4, 5].includes(step) && (
                <Button
                  onClick={() => handleBackClick()}
                  className={classes.backButton}
                >
                  {translations["back"]}
                </Button>
              )}

              {step === 1 && (
                <Button
                  disabled={disabledNext}
                  onClick={() => checkCourseId()}
                  className={disabledNext ? classes.disabledBtn : ""}
                  style={{ marginLeft: 10 }}
                >
                  {translations["next"]}
                </Button>
              )}
              {[2, 3, 4].includes(step) && (
                <Button
                  disabled={disabledNext}
                  onClick={() => handleNextClick()}
                  style={{ marginLeft: 10 }}
                >
                  {translations["next"]}
                </Button>
              )}
            </span>
            {step === 5 && (
              <Button
                disabled={disabledNext}
                onClick={e => launchCourseCreation(e)}
              >
                {editMode === "create"
                  ? translations.createCourse || "Create Course"
                  : translations.upadateCourse || "Update Course"}
              </Button>
            )}

            <Button
              className={classes.cancelButton}
              onClick={() => toggleDialog()}
            >
              {translations["cancel"]}
            </Button>
          </div>
        )}
      </>
    );
  }
}
CourseWizard.contextType = Context;
export default withStyles(styles)(CourseWizard);
