import React, { Component } from "react";
import { withStyles } from "@material-ui/styles";
import HeaderNav from "../../Components/HeaderNav";
import { Provider } from "../../Context";
import ProgramWizard from "../../Components/ProgramWizard/ProgramWizard";
import AlertDialog from "../../Components/AlertDialog";
import styles from "../../Assets/Quick_start_style.js";

const newProgramBasis = window.props && window.props.programBasis;
const translations = window.props && window.props.translations;

class NewProgramPage extends Component {
  state = {
    translations: window.props.translations,
    programs: window.props.programs || [],
    courses: window.props.courses || [],
    step: 1,
    selectedProgram: undefined,
    newProgram: newProgramBasis,
    cancelCreationPopup: false,
    newProgramResponse: undefined,
    blockedFields: []
  };


  addProgramDetails = programDetail => {
    this.setState(prevstate => ({
      newProgram: {
        ...prevstate.newProgram,
        [programDetail.name]: programDetail.value
      }
    }));
  };

  // PROGRAM CREATOR NAVIGATION
  handleNextClick = () => {
    this.setState(prevState => ({
      step: prevState.step + 1
    }));
  };

  handleBackClick = () => {
    this.setState(prevState => ({
      step: prevState.step - 1
    }));
  };

  toggleDialog = () => {
    this.setState(prevstate => ({
      cancelCreationPopup: !prevstate.cancelCreationPopup
    }));
  };

  // INPUTS EVENT TRACKER
  handleFieldChange = (fieldName, fieldValue) => {
    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: fieldValue,
          // RESET STATUS IN ORDER TO AVOID ERROR MESSAGE
        }
      };
    });
  };

  // COURSES SEARCH BAR EVENT TRACKER
  handleSearchBar = (fieldName, fieldValue) => {
    const { newProgram } = this.state
    let coursesResult = window.props.courses.filter(course =>
      course.course_id.includes(fieldValue) && !course.course_id.includes("duplicated") && course.course_id.includes(newProgram.program_org)
    );
    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: fieldValue,
        },
        courses: coursesResult
      };
    });
  };

  // COURSES SELECTOR
  handleSelectCourses = (fieldName, courseId) => {
    let coursesArray = [];
    if (!this.state.newProgram[fieldName]) {
      this.setState(prevState => {
        return {
          newProgram: {
            ...prevState.newProgram,
            [fieldName]: [],
            [`${fieldName}_bar`]: ""
          }
        };
      });
    }

    // MAKE SURE IT ITERATES ONLY WHEN ARRAY IS NOT EMPTY
    if (
      this.state.newProgram[fieldName] &&
      this.state.newProgram[fieldName].length > 0
    ) {
      coursesArray = [...this.state.newProgram[fieldName]];
    }
    if (!coursesArray.includes(courseId)) {
      coursesArray.push(courseId);
    }
    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: coursesArray,
          [`${fieldName}_bar`]: ""
        }
      };
    });

  };

  handleRemoveCourses = (fieldName, courseId) => {
    let coursesArray = [...this.state.newProgram[fieldName]];
    console.log(coursesArray.indexOf(courseId));
    if (coursesArray.includes(courseId)) {
      let index = coursesArray.indexOf(courseId);
      coursesArray.splice(index, 1);
    }
    this.setState(prevState => {
      return {
        newProgram: {
          ...prevState.newProgram,
          [fieldName]: coursesArray
        }
      };
    });
  };

  eraseNewProgram = () => {
    this.setState(
      {
        newProgram: {},
        step: 1,
        cancelCreationPopup: false,
        selectedCourses: []
      },
      (window.location.href = "/")
    );
  };

  // TO CHECK
  launchProgramCreation = async e => {
    e.preventDefault();
    this.handleNextClick();
    const url = window.props.api.programCreationUrl;
    const settings = {
      method: "POST",
      body: this.getFormData(),
      contentType: false,
      processData: false,
      headers: {
        "X-CSRFToken": window.props.csrfToken
      }
    };
    const api_call = await fetch(url, settings);
    const data = await api_call.json();
    if (data.code !== "404") {
      this.setState({ newProgramResponse: data });
    }
  };

  getFormData = () => {
    let formData = new FormData();

    Object.entries(this.state.newProgram).forEach(programInfo => {
      if (programInfo[0] !== 'courses_list_bar' && programInfo[0] !== "program_org") {
        if (programInfo[0] === 'program_type') {
          const optionsList = window.props.fields.find(elem => elem.name === programInfo[0]).options;
          // transform program_type array into individual paramameters, returning a Boolean (ex: {'is_linear': false})
          optionsList.map(option => formData.append(option, programInfo[1].includes(option)));
        } else if (programInfo[0] === 'courses_list') {

          let courses_id = []

          programInfo[1].map(course => {
            courses_id.push(course.course_id)
          })

          formData.append(programInfo[0], courses_id)

        } else {
          formData.append(programInfo[0], programInfo[1]);
        }
      }
    });
    return formData;
  };

  getContext = () => {
    return {
      ...this.state,
      handleSearchBar: this.handleSearchBar,
      handleFieldChange: this.handleFieldChange,
      handleNextClick: this.handleNextClick,
      addProgramDetails: this.addProgramDetails,
      toggleDialog: this.toggleDialog,
      handleBackClick: this.handleBackClick,
      launchProgramCreation: this.launchProgramCreation,
      eraseNewProgram: this.eraseNewProgram,
      deleteAsset: this.deleteAsset,
      handleSelectCourses: this.handleSelectCourses,
      handleRemoveCourses: this.handleRemoveCourses
    };
  };

  render() {
    const { classes } = this.props;
    return (
      <>
        <Provider value={this.getContext()}>
          <HeaderNav />
          <div className={classes.createPageWrapper}>
            <ProgramWizard />
          </div>
          <AlertDialog
            openDialog={this.state.cancelCreationPopup}
            toggleDialog={this.toggleDialog}
            dialogTitle={translations.cancelProgramTitle}
            dialogContent={translations.cancelProgramContent}
            dialogValid={translations.validCancelCreation}
            dialogCancel={translations.backToProgram}
            dialogAction={this.eraseNewProgram}
          />
        </Provider>
      </>
    );
  }
}

export default withStyles(styles)(NewProgramPage);
